<template>
  <div class="addGoods">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-alert
        title="添加商品信息"
        type="info"
        show-icon
        center
        finish-status="success"
      >
      </el-alert>
      <!-- 步骤条 -->
      <el-steps
        :active="active - 0"
        finish-status="success"
        align-center
        :space="200"
      >
        <el-step title="基本信息"></el-step>
        <el-step title="步商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>
      <!-- tab栏 -->
      <el-tabs
        tab-position="left"
        stretch
        :before-leave="beforeLeave"
        v-model="active"
        @tab-click="tabClicked"
      >
        <el-tab-pane label="基本信息">
          <AddForm ref="add" :addForm="addForm" />
        </el-tab-pane>
        <el-tab-pane label="商品参数">
          <div v-for="item in manyTableData" :key="item.attr_id">
            <h3>{{ item.attr_name }}</h3>
            <el-checkbox-group v-model="item.attr_vals">
              <el-checkbox
                :label="cb"
                v-for="(cb, i) in item.attr_vals"
                :key="i"
                border
              ></el-checkbox>
            </el-checkbox-group>
          </div>
        </el-tab-pane>
        <el-tab-pane label="商品属性">
          <div v-for="item in onlyTableData" :key="item.attr_id">
            <h3>{{ item.attr_name }}</h3>
            <el-input v-model="item.attr_vals"></el-input>
          </div>
        </el-tab-pane>
        <el-tab-pane label="商品图片">
          <el-upload action="#" list-type="picture-card" :auto-upload="false">
            <i slot="default" class="el-icon-plus"></i>
            <div slot="file" slot-scope="{ file }">
              <img
                class="el-upload-list__item-thumbnail"
                :src="file.url"
                alt=""
              />
              <span class="el-upload-list__item-actions">
                <span
                  class="el-upload-list__item-preview"
                  @click="handlePictureCardPreview(file)"
                >
                  <i class="el-icon-zoom-in"></i>
                </span>
                <span
                  v-if="!disabled"
                  class="el-upload-list__item-delete"
                  @click="handleDownload(file)"
                >
                  <i class="el-icon-download"></i>
                </span>
                <span
                  v-if="!disabled"
                  class="el-upload-list__item-delete"
                  @click="handleRemove(file)"
                >
                  <i class="el-icon-delete"></i>
                </span>
              </span>
            </div>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="" />
          </el-dialog>
        </el-tab-pane>
        <el-tab-pane label="商品描述">
          <mavon-editor v-model="text" :subfield="false" />
          <el-button type="primary" size="mini" @click="addGood"
            >添加商品</el-button
          >
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import AddForm from "../components/add/AddForm.vue";
import deep from "lodash/cloneDeep";
export default {
  components: { AddForm },
  data() {
    return {
      active: 0,
      isNext: false,
      addForm: {
        goods_name: "0",
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 商品所属的分类数组
        goods_cat: [],
        attrs: [],
      },
      // 静态参数列表数据
      onlyTableData: [],
      // 动态参数列表数据
      manyTableData: [],
      dialogVisible: false,
      dialogImageUrl: "",
      disabled: false,
      text: "",
      id: "",
    };
  },
  computed: {
    ...mapState({
      attributes: (state) => state.params.attributes,
    }),
  },
  methods: {
    change(value) {
      //console.log(value);
    },
    beforeLeave(activeName, oldActiveName) {
      // console.log(activeName, oldActiveName);
      if (oldActiveName === "0") {
        this.$refs.add.$refs.addForm.validate((valida) => {
          if (valida) {
            this.isNext = true;
          } else {
            this.isNext = false;
          }
        });
        this.id = this.addForm.goods_cat[2];
        return this.isNext;
      }
    },
    async tabClicked() {
      if (this.active === "1") {
        // 获取动态参数
        await this.getAttributes({
          id: this.id,
          sel: "many",
        });
        this.manyTableData = this.attributes.map((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(" ");
          return item;
        });
      } else if (this.active === "2") {
        // 获取静态参数

        await this.getAttributes({
          id: this.id,
          sel: "only",
        });
        this.onlyTableData = this.attributes;
      }
    },
    addGood() {
      const newAddForm = deep(this.addForm);
      newAddForm.goods_cat = newAddForm.goods_cat.join(",");
      // 处理动态参数
      this.manyTableData.forEach((item) => {
        const newInfo = {
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(" "),
        };
        newAddForm.attrs.push(newInfo);
      });
      // 处理静态属性
      this.onlyTableData.forEach((item) => {
        const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals };
        newAddForm.attrs.push(newInfo);
      });
      // console.log(this.addForm);
      this.addGoods({
        ...newAddForm,
        goods_introduce: this.text,
      }).then(
        (res) => {
          this.$message.success("添加成功");
          this.active += 2;
          this.$router.push("/goods");
        },
        (error) => {
          this.$message.error(error);
        }
      );
    },
    handleRemove(file) {
      console.log(file);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    },
    ...mapActions(["getAttributes", "addGoods"]),
  },
};
</script>

<style lang="less">
.el-breadcrumb {
  font-size: 12px;
  margin-bottom: 20px;
}
.addGoods {
  height: 100%;
  .el-steps {
    margin: 15px 0;
    font-size: 12px;
  }
  .el-step__title {
    font-size: 12px;
  }
  .el-button {
    margin-top: 15px;
  }
}
.el-cascader-menu {
  height: 200px;
}
.el-checkbox {
  margin: 0 10px 0 0 !important;
}
</style>
